.appHeader{
    background-color: white;
        padding: 0 15px;
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}

.actions {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    > * {
      margin-left: 30px;
      height: 100%;
      display: flex;
      align-items: center;
      .notice {
        display: block;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 22px;
        height: 22px;
        cursor: pointer;
      }
    }
}

.actions svg{
    width: 20px;
    height: 20px;
}